.profile-form{
  @include grid100gap(1.25em);
  &__fields{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.25em 1.5em;
  }
  &__bottom{
    display: grid;
    grid-template-columns: calc(100% - 24em) 22.5em;
    gap: 1.5em;
    @media screen and (min-width: 577px){
      .file-input{
        &__area{
          &-inner{
            text-align: center;
            flex-direction: column;
            min-height: 21.25em;
          }
        }
        &__icon{
          margin: 0 0 2.19em;
        }
        &__info{
          align-items: center;
        }
      }
    }
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    .m-btn{
      height: 2.5em;
      padding: 0 1.5em;
      img{
        width: 1em;
        flex-shrink: 0;
        margin-right: .5em;
      }
      span{
        font-size: 0.875em;
      }
    }
  }
}

.file-input{
  position: relative;
  &__preview{
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: .75em;
    z-index: 2;
    pointer-events: none;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      opacity: 0;
      transition: all .3s;
      object-fit: cover;
    }
  }
  &__area{
    position: relative;
    border-radius: 0.75em;
    background-color: $gray;
    padding: .625em;
    &-inner{
      border-radius: 0.75em;
      border: 1px dashed #E1E1E1;
      @include center;
      min-height: 9.375em;
      padding: 1em;
    }
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      cursor: pointer;
      &:hover{
        &~.file-input__area-inner{
          border-color: $black;
        }
      }
    }
  }
  &__icon{
    width: 7.5em;
    margin-right: 1.25em;
  }
  &__title{
    line-height: 120%;
    color: $black75;
  }
  &__info{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__btn{
    margin-top: .875em;
    padding: 0 1.5em 0 1em;
    border-radius: 0.75em;
    border: 1px solid #E6E6E6;
    height: 2.5em;
    font-weight: 500;
    color: $black75;
    img{
      width: 1em;
      margin-right: .5em;
      flex-shrink: 0;
    }
    span{
      font-size: 0.875em;
      line-height: 143%;
    }
  }
  &._uploaded{
    .image-preview{
      opacity: 1;
    }
    .file-input__preview{
      border: none;
      display: block;
      img{
        opacity: 1;
      }
    }
  }
}

.text-editor{
  padding: 1.25em;
  border-radius: 0.75em;
  background: #fff;
  box-shadow: 0 0 2em 0 rgba(0, 0, 0, 0.07);
  &__field{
    margin-top: 1.25em;
    textarea{
      min-height: 21.7em;
    }
  }
}


@media screen and (max-width: 992px){
  .profile-form{
    gap: .75em;
    &__fields{
      grid-template-columns: repeat(3,calc(100% / 3 - .5em));
      gap: 1.25em .75em;
    }
    &__bottom {
      display: grid;
      grid-template-columns: calc(100% - 11.75em) 11em;
      gap: .75em;
    }
  }

  .file-input{
    font-size: .6em;
  }

  .text-editor{
    padding: .81em .625em;
    &__field{
      margin-top: .69em;
      textarea{
        min-height: 19em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .profile-page{
    .banner__inner{
      display: none;
    }
  }

  .profile-form{
    gap: 1.31em;
    &__fields{
      grid-template-columns: 100%;
      gap: 1.25em .94em;
    }
    .fg{
      input{
        height: 4.875em;
      }
    }
    &__bottom{
      grid-template-columns: 100%;
      gap: 1.31em;
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .file-input{
    font-size: 1em;
  }
}
