.text31{
  font-size: 1.9375em;
  font-weight: 500;
  line-height: 120%;
}

.popular-channels{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__slider{
    width: 48.5em;
    display: grid;
    grid-template-columns: .625em calc(100% - 2.5em) .625em;
    gap: .625em;
    align-items: center;
  }
  .swiper{
    &-button{
      width: 100%;
      @include center;
      position: static;
      margin: 0;
      svg{
        width: 100%;
        height: auto;
      }
      &.swiper-button-disabled{
        opacity: .4;
      }
    }
    &-container{
      overflow: hidden;
    }
    &-slide{
      width: auto;
      padding-right: .94em;
    }
    &-scrollbar{
      display: none;
    }
  }
  .tag-link{
    height: 3.5625em;
    padding: 0 1.25em;
    span{
      font-size: 1.06em;
    }
  }
}

.news-collection{
  margin-top: 4.38em;
  &__grid{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9em));
    gap: 1.2em;
  }
}


@media screen and (max-width: 992px){
  .text31{
    font-size: 1.15em;
  }

  .popular-channels{
    margin-top: 1.31em;
    display: block;
    padding: 1em;
    box-shadow: $shadow;
    border-radius: .45em;
    &__slider{
      margin-top: .75em;
      width: 100%;
    }
  }

  .news-collection{
    margin-top: 1.125em;
    &__grid{
      margin-top: .875em;
      grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
      gap: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .popular-channels{
    padding: 0;
    box-shadow: unset;
    &__slider{
      grid-template-columns: 100%;
      gap: 0;
    }
    .swiper{
      &-container{
        overflow: unset;
      }
      &-slide{
        padding-right: .69em;
      }
      &-button{
        display: none;
      }
      &-scrollbar{
        display: block;
        margin-top: 1em;
      }
    }
    .tag-link{
      height: 2.375em;
      padding: 0 .625em;
      span{
        font-size: .6875em;
      }
    }
  }

  .news-collection{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .3125em));
    }
  }
}
