.page-layout{
  padding: 1.6em 0;
  &__inner{
    display: grid;
    grid-template-columns: 10.375em calc(100% - 10.375em - 25.9375em) 22.9375em;
    gap: 1.5em;
  }
  .burger-menu__group{
    text-align: left;
    &:not(:first-child){
      margin-top: 1.56em;
    }
  }
  .burger-menu__links{
    align-items: flex-start;
  }
  &__banners{
    margin-top: 1.625em;
    @include grid100gap(.625em);
    .banner__badge{
      top: .375em;
      left: .375em;
      height: .75em;
    }
  }
  .tags{
    position: static;
  }
}

.req{
  margin-top: 2.5em;
  border-radius: 0.75rem;
  background: #FFF;
  box-shadow: 0 0 2.125em 0 rgba(0, 0, 0, 0.07);
  padding: 1.38em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    width: 7.25em;
    display: block;
    flex-shrink: 0;
    margin-bottom: 1em;
  }
  &__btn{
    max-width: 100%;
    width: 16em;
    height: 2.625em;
    margin: 1em auto 0;
  }
}

.stories{
  margin-top: 2.125em;
  overflow: hidden;
  .swiper{
    &-container{
      width: calc(100% + .94em);
    }
    &-slide{
      width: 20%;
      padding-right: .94em;
    }
  }
}

.story-card{
  text-align: center;
  height: 22.875em;
  background-color: $primary;
  border-radius: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding: 1.5em;
  color: #fff;
  font-weight: 500;
  &__img{
    width: 4.1875em;
    height: 4.1875em;
    background-color: #fff;
    border-radius: 50%;
    margin-bottom: 1.06em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
      display: block;
    }
  }
}

.rubrics{
  padding: 1.5em 2.125em 3.625em;
  border-radius: 0.75em;
  background: #FFF;
  box-shadow: $shadow;
  margin-top: 3.69em;
  &__title{
    font-size: 1.8125em;
    font-weight: 500;
  }
  &__items{
    margin-top: 1em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
  }
  .tag-link{
    height: 3.5625em;
  }
}

.channel{
  margin-top: 1.06em;
  border-radius: 0.80144em;
  box-shadow: $shadow;
  overflow: hidden;
  &__top{
    padding: 1.33em;
    display: flex;
    align-items: center;
    .avatar{
      width: 4.5em;
      height: 4.5em;
      margin-right: 1.38em;
    }
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__name{
    font-size: 1.25em;
    line-height: 111%;
    font-weight: 500;
  }
  &__time{
    font-size: 1em;
    line-height: 130%;
    color: #A9A9A9;
  }
  &__subscribe{
    border-radius: 2.875em;
    height: 3.375em;
    padding: 0 2em;
    margin-left: auto;
    span{
      font-size: 1.2em;
    }
  }
  &__img{
    padding: 0 .625em;
    img{
      border-radius: .75em;
      display: block;
      width: 100%;
    }
  }
  &__content{
    max-width: 61em;
    padding: 1.75em 1em 1em;
  }
  &__title{
    font-size: 1.9375em;
    font-weight: 500;
    color: #3E3232;
  }
  &__desc{
    margin-top: 1.79em;
    font-size: 1em;
    line-height: 142%;
    color: $black75;
    @include truncate(6);
  }
  &__nav{
    margin-top: .5em;
    display: flex;
  }
  &__link{
    color: $primary;
    text-decoration: underline;
    font-size: 1em;
    line-height: 142%;
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: .25em;
  }
  &__tag{
    font-size: .875em;
    line-height: 130%;
    text-decoration: underline;
    color: $primary;
    margin: .625em .625em 0 0;
  }
  &__comment,
  &__stat,
  &__stat-item{
    display: flex;
    align-items: center;
  }
  &__stat{
    margin-top: 1.91em;
    &-item{
      color: $black75;
      margin-right: 2.25em;
      img{
        width: 2.6875em;
        flex-shrink: 0;
        margin-right: .53em;
      }
    }
  }
  &__comment{
    margin-top: 1.91em;
    color: $black75;
    img{
      width: 1.875em;
      margin-right: .55em;
    }
    span{
      font-size: .85em;
    }
  }
  &__news{
    padding: 1.5em 1.31em 2.75em;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      top: 0;
      right: -4.19em;
      height: 100%;
      width: 13.375em;
      background: linear-gradient(270deg, #D9D9D9 24.59%, #FFF 24.6%, rgba(255, 255, 255, 0.00) 100%);
      //opacity: .4;
      z-index: 3;
    }
    .swiper{
      &-slide{
        width: 20em;
        padding-right: 1.625em;
      }
    }
  }
}

.swiper-scrollbar{
  border-radius: 1.5625em;
  background: #F2F2F2;
  height: .3125em;
  margin-top: 2.5em;
  &-drag{
    border-radius: inherit;
    background: $primary;
  }
}

.swiper-scrollbar,
.swiper-scrollbar-drag{
  &:before{
    content: '';
    position: absolute;
    height: 400%;
    width: 100%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
}

.channel-item{
  padding: 1.625em 1.06em;
  height: 22.875em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  border-radius: 1em;
  background-color: $primary;
  &__btn{
    height: 3.5625em;
    width: 100%;
    padding: 0 1em;
    font-weight: 500;
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
}


@media screen and (max-width: 992px){
  .page-layout{
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__sidebar,
    &__sidebar2{
      display: none;
    }
  }

  .stories{
    margin-top: 1.3125em;
    .swiper{
      &-container{
        width: calc(100% + .56em);
      }
      &-slide{
        padding-right: .56em;
      }
    }
  }

  .story-card{
    height: 14.3125em;
    border-radius: .625em;
    padding: 1em;
    &__img{
      width: 2.625em;
      height: 2.625em;
      margin-bottom: .69em;
    }
    &__title{
      font-size: .6em;
    }
  }

  .rubrics{
    padding: 1em 1.375em 2.25em;
    border-radius: 0.46em;
    margin-top: 2.31em;
    &__title{
      font-size: 1.13em;
    }
    &__items{
      margin-top: .5em;
      grid-template-columns: repeat(4,calc(25% - .4em));
      gap: .44em .81em;
    }
    .tag-link{
      height: 2.25em;
      span{
        font-size: .69em;
      }
    }
  }

  .channel{
    margin-top: .69em;
    @include fullScreen;
    padding: 0;
    &__top{
      padding: .84em;
    }
    .avatar{
      width: 2.8125em;
      height: 2.8125em;
      margin-right: .84em;
    }
    &__name{
      font-size: 1em;
    }
    &__time{
      font-size: .75em;
    }
    &__subscribe{
      height: 2.125em;
      padding: 0 1.25em;
      span{
        font-size: .75em;
      }
    }
    &__content{
      padding: 1em .625em;
    }
    &__title{
      font-size: 1.25em;
    }
    &__desc{
      font-size: .875em;
      margin-top: .75em;
    }
    &__news{
      padding: .81em .81em 1.75em;
      &:after{
        right: -3em;
        width: 8.375em;
      }
      .swiper{
        &-slide{
          width: 12.5em;
          padding-right: 1.0625em;
        }
      }
    }
  }

  .channel-item{
    height: 14.3125em;
    padding: 1em .69em;
    border-radius: .625em;
    &__btn{
      height: 2.25em;
      border-radius: .45em;
      span{
        font-size: .75em;
      }
    }
  }

  .swiper-scrollbar{
    margin-top: 1.69em;
    height: .1875em;
  }
}


@media screen and (max-width: 576px){
  .page-layout{
    padding-top: 0;
    overflow: hidden;
    &__content{
      display: flex;
      flex-direction: column;
    }
  }

  .stories{
    overflow: unset;
    order: -1;
    margin: 0 0 1.12em;
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 8em;
        padding-right: .38em;
      }
    }
  }

  .story-card{
    height: 11.9375em;
    border-radius: .4em;
    padding: .6em;
    &__img{
      width: 2.125em;
      height: 2.125em;
      margin-bottom: .39em;
    }
  }

  .rubrics{
    margin-top: 1.125em;
    padding: 0;
    background-color: transparent;
    box-shadow: none;
    &__items{
      margin-top: .56em;
      max-width: unset;
      display: flex;
      gap: unset;
      @include fullScreen;
      @include scroll;
      overflow-x: auto;
      padding-bottom: 1em;
      &::-webkit-scrollbar {
        width: 0.1875em;
        height: 0.1875em;
      }
    }
    .tag-link{
      height: 2.375em;
    }
  }

  .channel{
    &__img{
      img{
        min-height: 16.25em;
        object-fit: cover;
      }
    }
    &__stat{
      margin-top: 1.19em;
      &-item{
        margin-right: 1.41em;
        img{
          width: 1.6875em;
          margin-right: .33em;
        }
        .text16{
          font-size: .75em;
        }
      }
    }
    &__comment{
      margin-top: 1em;
      img{
        width: 1.25em;
        margin-right: .29em;
      }
      span{
        font-size: .625em;
      }
    }
  }
}
