.news-page{
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 40em) 38.75em;
    gap: 1.25em;
  }
  &__top{
    padding: 2em;
    border-radius: 0.75em;
    background-color: $gray;
    margin-bottom: 8.125em;
  }
  &__img{
    width: 100%;
    display: block;
    margin: -6.25em 0 0;
    transform: translateY(8.25em);
    border-radius: .75em;
  }
  &__props{
    @include center;
  }
  &__prop{
    display: flex;
    align-items: center;
    color: $black75;
    @include lastM(0 4.94em 0 0);
    font-weight: 500;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
    span{
      font-size: 0.875em;
      line-height: 143%;
    }
  }
  .text{
    margin-top: 3.125em;
  }
}

.text{
  color: #3E3232;
  h4{
    font-size: 1.25em;
    line-height: 115%;
    margin-bottom: .75em;
    font-weight: 500;
  }
  p{
    font-size: 1.125em;
    line-height: 189%;
    @include lastM(0 0 .83em);
  }
  hr{
    height: 1.5em;
    display: block;
    opacity: 0;
  }
  img{
    width: 100%;
    display: block;
    max-width: 54.5em;
    margin: 0 auto .94em;
  }
}

.comments{
  margin-top: 4.375em;
  &__items{
    margin-top: 1.875em;
    @include grid100gap(2.5em);
  }
}

.comment-group{
  display: flex;
  flex-direction: column;
  &__btn{
    display: flex;
    align-items: center;
    margin: 1.25em 0 1.25em auto;
    color: rgba(15, 73, 161, 0.75);
    font-weight: 500;
    padding: .125em 0;
    border-bottom: 1px dashed rgba(15, 73, 161, 0.75);
    &:hover{
      color: rgba(15, 73, 161, 1);
      border-color: rgba(15, 73, 161, 1);
      path{
        fill-opacity: 1;
      }
    }
    span{
      font-size: .875em;
      line-height: 143%;
    }
    svg{
      width: 1em;
      height: auto;
      margin-left: .19em;
    }
    ._view2{
      display: none;
    }
  }
  &._open{
    .comment-group__btn{
      ._view2{
        display: block;
      }
      ._view1{
        display: none;
      }
      svg{
        transform: rotate(-180deg);
      }
    }
  }
}

.comment-card{
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__content{
    display: flex;
    align-items: center;
    .avatar{
      width: 3.75em;
      height: 3.75em;
      margin-right: .625em;
    }
  }
  &__info{
    @include grid100gap(.625em);
  }
  &__name{
    font-weight: 500;
    color: #3E3232;
    font-size: 1em;
    line-height: 120%;
  }
  &__btn{
    padding: 0 1em;
    border-radius: 0.75em;
    height: 2.5em;
    background-color: rgba(62, 50, 50, 0.05);
    font-weight: 500;
    color: $black75;
    &:hover{
      background-color: rgba(62, 50, 50, 0.25);
      color: $black75;
    }
    span{
      font-size: 0.875em;
      line-height: 143%;
    }
    svg{
      width: 1em;
      height: auto;
      margin-right: .5em;
    }
  }
  &__text{
    margin-top: .9375em;
    color: $black75;
    .text14{
      letter-spacing: 0.01563em;
    }
  }
}

.answers{
  order: 2;
  padding-left: 2.75em;
  border-left: 1px solid #D9D9D9;
  width: 54.5em;
  max-width: 100%;
  display: none;
  &__items{
    @include grid100gap(2.19em);
  }
  .comment-card{
    padding: 0;
    background-color: transparent;
  }
  .avatar{
    width: 2.8125em;
    height: 2.8125em;
  }
}

.new-comment{
  margin-top: 3.875em;
}

.comment-form{
  margin-top: 1.875em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .75em));
  gap: 1.5em;
  &__textarea{
    display: flex;
    flex-direction: column;
    textarea{
      flex-grow: 1;
    }
    @media screen and (min-width: 577px){
      grid-row: 1/3;
      grid-column: 2/3;
    }
  }
  &__bottom{
    grid-column: 1/-1;
    display: flex;
    justify-content: space-between;
  }
  .rating{
    margin-right: 2.44em;
  }
  &__btn{
    width: 9.125em;
    height: 3em;
    background: rgba(248, 21, 57, 0.75);
    border-radius: 0.75em;
    color: #fff;
    font-weight: 500;
    &:hover{
      background: rgba(248, 21, 57, 1);
      color: #fff;
    }
    span{
      font-size: 0.875em;
      line-height: 143%;
    }
    img{
      width: 1em;
      margin-right: .5em;
      flex-shrink: 0;
    }
  }
}

.rating{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .25em .875em;
  min-height: 3em;
  border-radius: 0.75em;
  background-color: $gray;
  flex-grow: 1;
  &__title{
    color: #3E3232;
    font-weight: 500;
  }
  &__items{
    display: flex;
    transform: translateX(1.13em);
  }
}

.rating-item{
  position: relative;
  &._red{
    input{
      &:checked{
        &~.rating-item__btn{
          background: #FC5C65;
        }
      }
    }
  }
  &._orange{
    input{
      &:checked{
        &~.rating-item__btn{
          background: #FA8231;
        }
      }
    }
  }
  &._yellow{
    input{
      &:checked{
        &~.rating-item__btn{
          background: #F7B731;
        }
      }
    }
  }
  &._blue{
    input{
      &:checked{
        &~.rating-item__btn{
          background: #45AAF2;
        }
      }
    }
  }
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked{
      &~.rating-item__btn{
        background: #26DE81;
        color: #fff;
        padding: 0 1em;
        svg{
          margin-right: .5em;
        }
        path{
          fill: #fff;
        }
        span{
          display: block;
        }
      }
    }
  }
  &__btn{
    border-radius: 0.75em;
    height: 2.5em;
    min-width: 2.5em;
    @include center;
    transition: all .3s;
    font-weight: 500;
    span{
      display: none;
      font-size: .875em;
      line-height: 143%;
    }
    svg{
      width: 1em;
      height: auto;
    }
  }
}

.news-feedback{
  display: flex;
  flex-wrap: wrap;
  margin-top: -.5em;
  &__btn{
    background-color: $gray;
    color: $black75;
    padding: 0 1.5em 0 1em;
    border-radius: 0.75em;
    height: 2.5em;
    font-weight: 500;
    @include lastM(0 .94em 0 0);
    margin-top: .5em !important;
    span{
      font-size: .875em;
    }
    img{
      width: 1em;
      margin-right: .5em;
      flex-shrink: 0;
    }
    &:hover{
      background-color: darken($gray,10%);
      color: $black75;
    }
  }
}

.author-card{
  margin-top: 1.56em;
  display: flex;
  padding: .94em;
  border-radius: 0.75em;
  background-color: $gray;
  .avatar{
    width: 5.4375em;
    height: 5.4375em;
    margin-right: .625em;
  }
  &__content{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__top{
    padding-top: .75em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__name{
    color: #3E3232;
  }
  &__num{
    color: $black75;
  }
  &__nav{
    margin-top: auto;
    padding-top: .5em;
    .m-btn{
      height: 2.5em;
      padding: 0 1.5em 0 1em;
      font-weight: 500;
      img{
        width: 1em;
        margin-right: .5em;
        flex-shrink: 0;
      }
      span{
        font-size: .875em;
      }
    }
  }
}

.news-info{
  margin-top: 1.56em;
  padding-right: 16.5em;
  position: relative;
  .news-banner2{
    position: absolute;
    top: 0;
    right: 0;
    width: 15.25em;
    max-height: 100%;
    margin: 0;
    img{
      object-fit: cover;
    }
  }
}

.popular-news,
.news-tags{
  padding: 1.25em .94em;
  border-radius: 0.75em;
  background: $gray;
}

.news-tags{
  &__links{
    margin-top: .45em;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 1em);
  }
  &__link{
    font-weight: 500;
    color: $black75;
    margin: .875em .875em 0 0;
    span{
      font-size: 0.8125em;
      line-height: 120%;
    }
  }
}

.popular-news{
  margin-top: 1.56em;
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
}

.news-card3{
  display: flex;
  align-items: center;
  .avatar{
    width: 5.4375em;
    height: 5.4375em;
    border-radius: .75em;
    margin-right: .625em;
  }
  &__content{
    @include grid100gap(.375em);
  }
  &__title{
    font-weight: 500;
    color: #3E3232;
    line-height: 153%;
  }
  &__desc{
    color: $black75;
    line-height: 150%;
    letter-spacing: 0.01563em;
  }
}

.news-banner2{
  border-radius: 0.75em;
  filter: blur(.3125em);
  margin-top: 1.56em;
  img{
    width: 100%;
    display: block;
    border-radius: inherit;
  }
}


@media screen and (max-width: 992px){
  .news-page{
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__sidebar{
      display: none;
    }
    &__top{
      padding: 1.19em 1.19em 0;
      border-radius: 0.45em;
      margin-bottom: 4.94em;
      .text36{
        font-size: 1.375em;
      }
    }
    &__img{
      margin: -2.5em 0 0;
      border-radius: .45em;
      transform: translateY(3.75em);
    }
    &__prop{
      @include lastM(0 2.98em 0 0);
      img{
        width: .6em;
        margin-right: .3em;
      }
      span{
        font-size: 0.625em;
      }
    }
  }

  .text{
    font-size: .875em;
  }

  .comments{
    margin-top: 1.12em;
    &__items{
      gap: 1em;
    }
  }

  .comment-group{
    font-size: .8125em;
  }

  .answers{
    width: 64em;
  }

  .new-comment{
    margin-top: 2.69em;
    .line-title{
      display: none;
    }
  }

  .comment-form{
    margin: 0;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    .fg{
      input{
        height: 4.875em;
      }
    }
    &__bottom{
      display: block;
      .rating{
        margin: 0 0 1em;
      }
    }
    &__btn{
      margin-left: auto;
    }
  }

  .rating{
    padding: 0 .875em;
    &__title{
      font-size: .75em;
    }
    &__items{
      transform: unset;
    }
  }

  .news-feedback__btn{
    //font-size: .625em;
  }
}


@media screen and (max-width: 576px){
  .news-page{
    &__top{
      padding: .94em .69em 0;
      margin-bottom: 5.56em;
      .text36{
        font-size: 1.0625em;
      }
    }
    &__img{
      margin-top: -3.25em;
      transform: translateY(4.56em);
    }
    &__props{
      padding: 0 .75em;
      justify-content: space-between;
    }
    &__prop{
      @include lastM(0);
    }
  }

  .comments{
    margin-top: 2.19em;
   .line-title{
     display: none;
   }
    &__items{
      margin: 0;
    }
  }

  .answers{
    width: calc(100% - 2em);
    &~.comment-card{
      padding-bottom: 7.5em;
    }
  }

  .comment-form{
    grid-template-columns: 100%;
    .fg{
      textarea{
        height: 12.875em;
      }
    }
    &__btn{
      width: 100%;
    }
  }

  .rating{
    display: block;
    padding: .75em;
    &__title{
      font-size: 1.25em;
      margin: 0 0 .25em;
    }
  }

  .news-feedback__btn{
    //font-size: .59em;
  }
}
