.error404{
  padding: 7em 0;
  &__inner{
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__num{
    font-size: 12em;
    line-height: 118%;
    text-shadow: 0 0 44px #BDD6ED;
    font-weight: 500;
    color: #1B92D1;
  }
  &__text{
    font-size: 1em;
    line-height: 119%;
    font-weight: 500;
    color: $black75;
    margin-top: 1.875em;
  }
}


@media screen and (max-width: 992px){
  .error404{
    &__num{
      font-size: 7em;
    }
    &__text{
      margin-top: .75em;
    }
  }
}
