@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 3.75em;
    font-weight: 500;
    border-radius: 0.75em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 111%;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-white{
    background-color: #fff;
    color: $primary;
    &:hover{
        background-color: darken(#fff,10%);
        color: $primary;
    }
}

.m-btn-primary-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
    &:disabled{
        background: #E3E3E3;
        border-color: #E3E3E3;
        color: #fff;
        pointer-events: none;
    }
}

.m-btn-gray{
    background-color: #DBDBDB;
    color: #fff;
    &:hover{
        background-color: darken(#DBDBDB,10%);
        color: #fff;
    }
}
