.categories-page{
  .banner{
    margin: 0;
    grid-column: 1/-1;
    .container{
      padding: 0;
    }
  }
  .news-items{
    margin-top: .5em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.375em;
    .m-btn{
      width: 15.0625em;
    }
  }
}

.filter{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 2.81em;
  padding: .2em .69em .2em 1em;
  background-color: $gray;
  border-radius: 0.75em;
  min-height: 3em;
  &__links{
    display: flex;
    align-items: center;
  }
  &__link{
    color: rgba(62, 50, 50, 0.75);
    font-weight: 500;
    @include lastM(0 .94em 0 0);
    display: flex;
    align-items: center;
    span{
      font-size: 0.8125em;
      line-height: 116%;
    }
    &._active{
      color: $primary;
    }
  }
}




@media screen and (max-width: 992px){
  .categories-page{
    &__nav{
      justify-content: unset;
      .m-btn{
        width: calc(50% - 0.9375em / 2);
        height: 2.375em;
        border-radius: .46888em;
        span{
          font-size: .71em;
        }
      }
    }
    .news-sidebar__items{
      margin-top: 1.44em;
    }
  }

  .filter{
    margin-bottom: 1.25em;
    min-height: 1.875em;
    border-radius: 0.46875em;
    padding: .19em .25em .19em .875em;
    &__link{
      @include lastM(0 .625em 0 0);
      span{
        font-size: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .categories-page{
    .main-news__inner{
      gap: 1.75em;
    }
    &__content{
      .line-title{
        display: none;
      }
    }
    .news-items{
      margin-top: 0;
      width: auto;
      padding: 0;
      margin-left: 0;
      overflow: unset;
      @include grid100gap(.9375em);
    }
    .banner{
      display: none;
    }
    .news-card{
      width: 100%;
      margin: 0 !important;
      &__img{
        padding-bottom: 47.1%;
      }
    }
    &__nav{
      margin-top: 1.75em;
      .m-btn{
        width: 100%;
      }
    }
  }

  .filter{
    margin-bottom: .94em;
  }
}
