.header{
  padding: 1.62em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 16.88em;
    img{
      width: 100%;
      display: block;
    }
  }
  .m-search{
    width: 24.9375em;
    max-width: 100%;
  }
}

.m-rect{
  width: 3em;
  height: 3em;
  border-radius: 0.75em;
  @include center;
  background-color: $gray;
}

.menu{
  display: flex;
  align-items: center;
}

.m-search{
  position: relative;
  &__more,
  &__confirm{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1.25em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
  &__more{
    left: 1em;
  }
  &__confirm{
    right: 1em;
    width: 1.375em;
  }
  &__field{
    input{
      padding: 0 4.67em;
    }
  }
}

.burger-menu,
.search-toggle,
.burger-menu__bg,
.mob-menu,
.mob-menu__bg,
.burger{
  display: none;
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.burger-menu{
  &__group{
    margin-top: 2.81em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 111%;
    font-weight: 500;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1em;
    text-align: center;
  }
  &__link{
    color: $black75;
    @include lastM(0 0 .625em);
    span{
      font-size: .75em;
      line-height: 143%;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.12em 0;
    &__logo{
      width: 10.5625em;
    }
    .m-search{
      width: 15.6em;
      margin-right: 1.56em;
    }
  }

  .m-rect{
    width: 1.875em;
    height: 1.875em;
    border-radius: 0.47em;
  }

  .m-search{
    &__confirm,
    &__more{
      width: .875em;
    }
    &__more{
      left: .625em;
    }
    &__confirm{
      right: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: 1.31em 0;
    position: relative;
    z-index: 10;
    &__inner{
      justify-content: center;
      position: relative;
    }
    &__logo{
      width: 12.9375em;
    }
    .m-search{
      width: 100%;
    }
    &._open-tag-menu{
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
    }
    &._open-search{
      .m-search{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &._open{
      .burger-menu{
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
      .burger{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
  }

  .m-search{
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 100%;
    left: 0;
  }

  .burger{
    display: flex;
    width: 2.3125em;
    height: 2.3125em;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    border-radius: 0.125em;
    z-index: 10;
    img{
      width: 1.5em;
      @include centerAbsolute;
    }
    ._view2{
      display: none;
      width: 2em;
    }
  }

  .mob-menu,
  .burger-menu{
    position: fixed;
    background: #FFF;
    bottom: 0;
    right: 0;
    box-shadow: 0px 0px 32px 0px rgba(0, 0, 0, 0.07);
    display: flex;
    flex-direction: column;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg{
      background: rgba(0, 0, 0, 0.50);
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      z-index: 10;
    }
  }

  .mob-menu{
    width: 100%;
    min-height: 22.0625em;
    z-index: 11;
    padding: .81em .75em .69em;
    &__tabs{
      display: flex;
      margin-top: auto;
      padding-top: .375em;
    }
    &__title,
    &__link{
      display: flex;
      align-items: center;
      &:before{
        content: '';
        flex-shrink: 0;
        width: 0.15631em;
        height: 0.39075em;
        border-radius: 0.46888em;
        margin-right: .23em;
        background-color: $primary;
        transition: all .3s;
      }
    }
    &__title{
      padding: 1em 0;
      border-bottom: 1px solid #E6E6E6;
      margin-bottom: .5em;
    }
    &__items{
      width: calc(100% + 1.5em);
      margin-left: -.75em;
      padding-right: .25em;
    }
    &__links{
      @include grid100gap(.25em);
      padding-right: .375em;
      max-height: 14em;
      overflow-y: auto;
      @include scroll;
    }
    &__link{
      padding: 0 .875em;
      height: 3.3125em;
      &:not(._active){
        &:before{
          opacity: 0;
          margin: 0;
          width: 0;
        }
      }
      &._active{
        background: #F8F8F8;
      }
    }
  }

  .menu-tab{
    background-color: #E7E7E7;
    width: 2.3125em;
    height: 2.3125em;
    @include center;
    flex-direction: column;
    font-weight: 500;
    border-radius: .125em;
    color: #fff;
    margin-right: .875em;
    transition: all .3s;
    &._active{
      background-color: $primary;
    }
    &__icon{
      height: .625em;
      margin-bottom: .5em;
      display: flex;
      align-items: flex-end;
      justify-content: center;
      img{
        height: 100%;
        width: auto;
      }
      &._small{
        img{
          height: .5em;
        }
      }
    }
    &__title{
      font-size: .375em;
      letter-spacing: 0.00975em;
    }
  }

  .burger-menu{
    z-index: 9;
    height: $vh;
    width: calc(100% - 1.81em);
    align-items: center;
    padding: 6.94em 2em;
    overflow-y: auto;
    &__bg{
      //display: block;
      z-index: 8;
    }
    &__logo{
      width: 12.9375em;
      img{
        width: 100%;
        display: block;
      }
    }
    .menu-tab{
      position: fixed;
      left: 3.25em;
      bottom: 1em;
    }
  }

  .search-toggle{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    @include center;
    width: 1.375em;
    svg{
      width: 100%;
      height: auto;
    }
  }
}
