@import "../vars.scss";
.breadcrumbs {
    margin: 2.75em 0;
    &__item {
        position: relative;
        display: inline;
        &:last-child {
            color: rgba(62, 50, 50, 0.50);
        }
        &:not(:first-child) {
            margin-left: 1em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 1em;
                height: .875em;
                top: .1875em;
                right: 100%;
                background-image: url(../img/arrow-right2.svg);
                @include bgImgSize(100%);
            }
        }
        span{
            font-size: .75em;
            line-height: 117%;
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs:not(._not-hidden){
        display: none;
    }
}
